.fill {
  background-color: rgba(255, 255, 255, 0.35);
  position: absolute;
  left: 0;
  top: 0;
  height: 100%;
  z-index: 1;
  -webkit-animation: progressAnimiation 2s linear infinite;
          animation: progressAnimiation 2s linear infinite;
  max-width: 100%;
  overflow: hidden;
  -webkit-transition: 1s ease-in;
  transition: 1s ease-in;
}

.fill:before, .fill:after {
  content: '';
  position: absolute;
  width: 1px;
  top: -10px;
  height: 15px;
  right: 0;
  background: #fdfdfd9e;
  box-shadow: 0 0 2px 1px #ffffff89;
  -webkit-animation: glow 1000ms linear infinite normal;
          animation: glow 1000ms linear infinite normal;
}

.fill:after {
  -webkit-animation-delay: 500ms;
          animation-delay: 500ms;
}

.reservation {
  background-color: #c9dfeb !important;
}

.over-due {
  background-color: #f68e8e;
}

@-webkit-keyframes progressAnimiation {
  0% {
    background-position: 0 0;
  }
  100% {
    background-position: 35px 35px;
  }
}

@keyframes progressAnimiation {
  0% {
    background-position: 0 0;
  }
  100% {
    background-position: 35px 35px;
  }
}

@-webkit-keyframes glow {
  0% {
    top: -10px;
  }
  100% {
    top: 110%;
  }
}

@keyframes glow {
  0% {
    top: -10px;
  }
  100% {
    top: 110%;
  }
}

@-webkit-keyframes partyCardSlideIn {
  from {
    -webkit-transform: scale(0);
            transform: scale(0);
  }
  to {
    -webkit-transform: scale(1);
            transform: scale(1);
  }
}

@keyframes partyCardSlideIn {
  from {
    -webkit-transform: scale(0);
            transform: scale(0);
  }
  to {
    -webkit-transform: scale(1);
            transform: scale(1);
  }
}
